import Link from 'next/link';
import { usePathname } from 'next/navigation';

type Props = {
  currentUserId: number | null;
};

export default function Header({ currentUserId }: Props) {

  const path = usePathname();
  console.log(path);

  return (
    <header className="w-full bg-white shadow-sm border-b border-gray-200">
      <div className="max-w-7xl mx-auto h-14 px-6 flex items-center justify-between ">
        {/* 左側 */}
        <nav className="flex items-center gap-6">
          <Link href="/" className="font-bold text-xl text-gray-800 hover:text-gray-900">
            ロゴだよ
          </Link>
          <Link href="/videos" className="text-xl text-gray-600 hover:font-semibold duration-100">
            投稿一覧
          </Link>
          <Link
            href="/posts/create"
            className="text-xl text-gray-600 hover:font-semibold duration-100"
          >
            新規投稿
          </Link>
        </nav>

        {/* 右側 */}
        {currentUserId ? (
          <Link
            href={`/users/me`}
            className="text-xl text-gray-600 hover:font-semibold duration-100"
          >
            マイページ
          </Link>
        ) : (
          <nav className="flex items-center gap-6">
            <Link
              href="/login"
              className="text-gray-600 border p-2 rounded hover:font-semibold duration-100"
            >
              ログイン
            </Link>
            <Link
              href="/users/create"
              className="border p-2 rounded bg-gray-800 text-white hover:font-semibold duration-100"
            >
              新規登録
            </Link>
          </nav>
        )}
      </div>
    </header>
  );
}
